<template>
  <div id="app">
    <div ref="chartContainer"></div>
  </div>
</template>

<script>
import Highcharts from 'highcharts';
import Highcharts3D from 'highcharts/highcharts-3d';

Highcharts3D(Highcharts);

export default {
  name: 'App',
  data() {
    return {
      result: null,
      chartOptions: {
        chart: {
          type: 'column',
          options3d: {
            enabled: true,
            alpha: 10,
            beta: 34,
            depth: 86,
            viewDistance: 25,
            zoomType: 'xy',
            width: '100%', // 设置图表宽度
            height: '100%' // 设置图表高度
          },
          backgroundColor: 'rgba(19, 25, 47, 0.6)'
        },
        title: {
          text: '汽车品牌销售柱状图',
          style: {
            color: '#ffffff' // 设置标题文字颜色为白色
          }
        },
        xAxis: {
          type: 'category',
          labels: {
            style: {
              color: '#ffffff' // 设置 x 轴标签文字颜色为白色
            }
          }
        },
        yAxis: {
          title: {
            enabled: false
          },
          labels: {
            style: {
              color: '#ffffff' // 设置 x 轴标签文字颜色为白色
            }
          }
        },
        tooltip: {
          headerFormat: '<b>{point.key}</b><br>',
          pointFormat: 'Cars sold: {point.y}'
        },
        legend: {
          enabled: false
        },
        plotOptions: {
          column: {
            depth: 25
          }
        },
        series: [{
          data: [
            ['Toyota', 1795],
            ['Volkswagen', 1242],
            ['Volvo', 1074],
            ['Tesla', 832],
            ['Hyundai', 593],
            ['MG', 509],
            ['Skoda', 471],
            ['BMW', 442],
            ['Ford', 385],
            ['Nissan', 371]
          ],
          colorByPoint: true
        }]
      }
    };
  },
  async mounted() {
    this.chart = Highcharts.chart(this.$refs.chartContainer, this.chartOptions);
    this.result = await this.$request.get("cars/centerRight1");
    this.result = this.result.data;
    // 按照 value 进行排序
    // this.result = this.result.sort((a, b) => b.value - a.value);
    this.result = this.result.slice(4, 14)
    const series = this.chart.series[0];
    series.setData(this.result.map(item => [item.name, item.value]));
  }
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;

}


</style>