<template>
  <div>
    <img :src="this.result" width="100%">
  </div>
</template>

<script>
export default {
  data() {
    return {
      result: ""
    }
  },
  async mounted() {
    this.result = await this.$request.get("cars/centerLeft2");
    this.result = this.result.data;
    this.result="data:image/png;base64,"+this.result;
  }
}
</script>

<style lang="scss" scoped>
img {
  padding-top: 2rem;
  width: 100%;
  height: 40vh;
}
</style>