<template>
  <div id="center">
    <div class="up">
      <div class="bg-color-black item">
        <p class="ml-3 colorBlue fw-b fs-xl">车辆总数据</p>
        <span class="dv-dig-flop ml-1 mt-2 pl-3 fzl">{{ this.result.cars_total }}</span>
      </div>
      <div class="bg-color-black item">
        <p class="ml-3 colorBlue fw-b fs-xl">销量最多车型</p>
        <span class="dv-dig-flop ml-1 mt-2 pl-3 fzl">{{ this.result.max_sale_car_name }}</span>
      </div>
      <div class="bg-color-black item">
        <p class="ml-3 colorBlue fw-b fs-xl">车辆最高销售额</p>
        <span class="dv-dig-flop ml-1 mt-2 pl-3 fzl">{{ this.result.max_sale_volumn + "辆" }}</span>
      </div>
      <div class="bg-color-black item">
        <p class="ml-3 colorBlue fw-b fs-xl">销售最多车型</p>
        <span class="dv-dig-flop ml-1 mt-2 pl-3 fzl">{{ this.result.max_sale_car }}</span>
      </div>
      <div class="bg-color-black item">
        <p class="ml-3 colorBlue fw-b fs-xl">车型最多品牌</p>
        <span class="dv-dig-flop ml-1 mt-2 pl-3 fzl">{{ this.result.max_car_model }}</span>
      </div>
      <div class="bg-color-black item">
        <p class="ml-3 colorBlue fw-b fs-xl">车辆平均价格</p>
        <span class="dv-dig-flop ml-1 mt-2 pl-3 fzl">{{ this.result.cars_average_price + "W" }}</span>
      </div>
    </div>
    <div class="down">
      <div class="ranking bg-color-black">
        <span>
          <icon name="chart-pie" class="text-icon"></icon>
        </span>
        <span class="fs-xl text mx-2 mb-1 pl-3">汽车品牌销售排行榜</span>
        <dv-scroll-ranking-board class="dv-scr-rank-board mt-1" v-bind:config="ranking"
                                 v-bind:key="ranking.data[0].value"/>
      </div>
      <div class="percent">
        <div class="item bg-color-black">
          <span>油车销售占比</span>
          <CenterChart
              :id="rate[0].id"
              :tips="rate[0].tips"
              :colorObj="rate[0].colorData"
          />
        </div>
        <div class="item bg-color-black">
          <span>电车销售占比</span>
          <CenterChart
              :id="rate[1].id"
              :tips="rate[1].tips"
              :colorObj="rate[1].colorData"
          />
        </div>
        <div class="water">
          <span style="padding-left: 0.4rem;font-size: 0.85rem">油电混合动力占比</span>
          <dv-water-level-pond class="dv-wa-le-po" :config="water" :key="water.data[0]"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CenterChart from '@/components/echart/center/centerChartRate'

export default {
  data() {
    return {
      result: "",
      rank: [],
      ranking: {
        data: [
          {
            "name": "比亚迪",
            "value": 33
          }
        ],
        carousel: 'single',
        unit: '类'
      },
      water: {
        data: [14],
        shape: 'roundRect',
        formatter: '{value}%',
        waveNum: 3
      },
      // 通过率和达标率的组件复用数据
      rate: [
        {
          id: 'centerRate1',
          tips: 60,
          colorData: {
            textStyle: '#3fc0fb',
            series: {
              color: ['#00bcd44a', 'transparent'],
              dataColor: {
                normal: '#03a9f4',
                shadowColor: '#97e2f5'
              }
            }
          }
        },
        {
          id: 'centerRate2',
          tips: 40,
          colorData: {
            textStyle: '#67e0e3',
            series: {
              color: ['#faf3a378', 'transparent'],
              dataColor: {
                normal: '#ff9800',
                shadowColor: '#fcebad'
              }
            }
          }
        }
      ]
    }
  },
  async mounted() {
    this.result = await this.$request.get("cars/centerData");
    this.result = this.result.data;


    // 处理品牌销售排名
    this.rank = Object.keys(this.result.cars_brand_sale_rank).map(brand => ({
      name: brand,
      value: this.result.cars_brand_sale_rank[brand]
    }));
    this.$set(this.ranking, "data", this.rank);

    this.rate[0].tips = this.result.type_ratios.汽油
    this.rate[1].tips = this.result.type_ratios.纯电动
    this.water.data = [this.result.type_ratios.插电式混合动力]
    // console.log(this.water.data)
  }
  ,
  components: {
    CenterChart
  }
}
</script>

<style lang="scss" scoped>
.fzl {
  font-size: larger;
  line-height: 2;
  padding-left: 2rem;
}

#center {
  display: flex;
  flex-direction: column;

  .up {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    .item {
      border-radius: 6px;
      padding-top: 8px;
      margin-top: 8px;
      width: 32%;
      height: 70px;

      .dv-dig-flop {
        width: 150px;
        height: 30px;
      }
    }
  }

  .down {
    padding: 6px 4px;
    padding-bottom: 0;
    width: 100%;
    display: flex;
    height: 255px;
    justify-content: space-between;

    .bg-color-black {
      border-radius: 5px;
    }

    .ranking {
      padding: 10px;
      width: 59%;

      .dv-scr-rank-board {
        height: 225px;
      }
    }

    .percent {
      width: 40%;
      display: flex;
      flex-wrap: wrap;

      .item {
        width: 50%;
        height: 120px;

        span {
          margin-top: 8px;
          font-size: 14px;
          display: flex;
          justify-content: center;
        }
      }

      .water {
        width: 100%;

        .dv-wa-le-po {
          height: 120px;
        }

      }
    }
  }
}
</style>
