          { value: 5, name: "data2" },
          { value: 15, name: "data3" },
          { value: 25, name: "data4" },
          { value: 20, name: "data5" },
          { value: 35, name: "data6" }
        ]
      }
    }
  },
  components: {
    Chart,
  },
  async mounted() {
    this.result = await this.$request.get("cars/centerRight1")
    this.result = this.result.data
    this.result = this.result.slice(0, 13)
    this.cdata.seriesData = this.result
    console.log(this.cdata.seriesData)
  },
  methods: {
  }
}
</script>

