<template>
  <div id="bottomRight">
    <div class="bg-color-black">
      <div class="d-flex pt-2 pl-2">
        <span>
          <icon name="chart-area" class="text-icon"></icon>
        </span>
        <div class="d-flex">
          <span class="fs-xl text mx-2">汽车销量Top100</span>
          <div class="decoration2">
            <dv-decoration-2 :reverse="true" style="width:5px;height:6rem;"/>
          </div>
        </div>
      </div>
      <div class="row-list">
        <ul class="car-rank" style="width:100%;height:420px;overflow:auto">
          <li style="font-size: 23px">
            <div>销售排名</div>
            <div>图片</div>
            <div>汽车信息</div>
            <div>销售价格</div>
            <div>销售趋势</div>
            <div>保修时间</div>
            <div>上市时间</div>
          </li>
          <li v-for="car in cars" :key="car.rank">
            <div>{{ car.rank }}</div>
            <div><img :src="car.carImage" :alt="car.carName" style="height: 100%;width: 100%"></div>
            <div>
              <p>{{ car.carName }}</p>
              <p>{{ car.manufacturer }}/{{ car.brand }}</p>
            </div>
            <div class="car-style">{{ car.price["max"] }}万元</div>
            <div class="car-style">{{ car.saleVolume }}</div>
            <div class="car-style">{{ car.insure }}</div>
            <div class="car-style">{{ car.marketTime }}</div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cars: []
    }
  },
  async mounted() {
    this.cars = await this.$request.get("cars/bottomLeft");
    this.cars = this.cars.data;
    this.cars=this.cars[0].slice(0, 100);
    
  }
};
</script>

<style lang="scss" class>
$box-height: 520px;
$box-width: 100%;
#bottomRight {
  padding: 14px 16px;
  height: $box-height;
  width: $box-width;
  border-radius: 5px;

  .bg-color-black {
    height: $box-height - 30px;
    border-radius: 10px;
  }

  .text {
    color: #c3cbde;
  }

  //下滑线动态
  .decoration2 {
    position: absolute;
    right: 0.125rem;
  }

  .chart-box {
    margin-top: 16px;
    width: 170px;
    height: 170px;

    .active-ring-name {
      padding-top: 10px;
    }
  }

  .car-style {
    line-height: 60px;
  }

  .row-list {
    list-style: none;

    .car-rank::-webkit-scrollbar {
      display: none;
    }

    .car-rank li {
      display: grid;
      -ms-grid-columns: 100px 150px 180px 120px 120px 110px 100px;
      grid-template-columns: 100px 150px 180px 120px 120px 110px 100px;
      cursor: pointer;
      margin-left: 23px;
      text-align: center;
      line-height: 30px;
    }

    .car-rank .list_index {
      font-size: 21px;
      line-height: 70px;
      font-weight: bold;
    }

    .car-rank .list_img {
      width: 156px;
      height: 80px;
    }

  }
}
</style>